
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonBackButton,
  IonFab,
  IonFabButton,
  IonIcon,
  modalController,
} from "@ionic/vue";
import { computed, defineComponent } from "vue";
import { useStore } from "vuex";
import { camera } from 'ionicons/icons';
import { usePhotoGallery } from '@/composables/usePhotoGallery';
import ImageModal from '../Reusables/ImageModal.vue'
export default defineComponent({
  emits: ["login", "createAccount"],
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonBackButton,
    IonFab,
    IonFabButton,
    IonIcon,
  },
  setup() {
    const store = useStore()
    const user = computed(() => store.state.users.user)
    const gallery = computed(() => store.state.users.gallery)
    const { takePhoto, photos } = usePhotoGallery();
    const takePicture = () => {
        takePhoto(user.value?.id, true).then(async (image) => {
            await store.dispatch('users/saveGallery', image)
        })
    }
    const openModal = async (imageSrc: any) => {
      const modal = await modalController
        .create({
          component: ImageModal,
          cssClass: 'image-modal',
          showBackdrop: true,
          swipeToClose: true,
          animated: true,
          componentProps: {
            imageSrc: imageSrc,
            modalCLose: () => modal.dismiss()
          },
        })
      return modal.present();
    }
    return {
        user,
        camera,
        photos,
        takePicture,
        gallery,
        openModal
    };
  },
});
